<template>
  <div class="about">
    <!-- <div class="aboutbanner"> -->
      <!-- <div class="info">
        <div class="txt">
          <h3 class="tle"></h3>
          <p><span></span></p>
          <p><span></span></p>
          <p><span></span></p>
          <p><span></span></p>
        </div>
      </div> -->

    <!-- </div> -->
    <div class="basicwrap translucent6">
      <div class="basicbox">
        <p>2016</p>
        <p></p>
        <p>nodejs</p>
        <p></p>
      </div>
    </div>

    <div class="skillwrap w1280 translucent6">
      <div class="skillbox">


      </div>
      <div class="echartswrap">
        <div class="echartsbox" id="skillsecharts"></div>
      </div>
    </div>




  </div>
</template>

<script>


import echarts from 'echarts'
	export default {
		data() {
			return {
        skillsecharts:null,

			}
		},
		computed:{

    },
		methods: {


		},
		mounted() {
    this.skillsecharts = echarts.init(document.getElementById('skillsecharts'));
    this.skillsecharts.setOption({
      color: ['#0078D7'],
      tooltip : {
        trigger: 'axis',
        axisPointer : {            // 
          type : 'shadow' ,
          color:['#fff']       // 'line' | 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis : [
        {
          type : 'category',
          data : ['ps', 'vue', 'node', 'h5', 'css', 'sass',],
          axisTick: {
            alignWithLabel: true
          }
        }
      ],
      yAxis : [
        {
          // type : 'category',
          // data : ['10','20','30','40'],
          axisTick: {
            alignWithLabel: true
          }
        }
      ],
      series : [
        {
          name:'',
          type:'bar',
          barWidth: '40%',
          data:[70, 85, 80, 90, 90, 75]
        },

      ],
      label: {
        normal: {
          show: true,
          position: 'top',
          formatter: '{c}'
        }
      },
      itemStyle: {
        normal: {

          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            // color: 'rgba(17, 168,171, 1)'
            color: 'rgba(0, 120, 215, 1)'
          }, {
            offset: 1,
            color: 'rgba(0, 120, 215, 0.1)'
          }]),
          shadowColor: 'rgba(0, 0, 0, 0.1)',
          shadowBlur: 10
        }
      }
    }

    );

		}
	}


</script>



